@import "../../Components/commonStyle.less";

.homePage-phone {
    p {
        margin: 0;
        padding: 0;
    }

    position: relative;
    width: 100%;
    background-color: rgba(19, 20, 44, 1);

    // 头部样式
    main {
        position: relative;
        width: 100vw;
        height: 100*1.52vw;
        background: url('../../Assets/header/phoneBannerBg@2x.png') no-repeat;
        background-size: 100%;
        margin-bottom: 2rem;

        hgroup {
            position: relative;
            text-align: center;
            z-index: 3;
            padding-top: 11.7rem;

            .banner-p1 {
                font-size: 2.97rem;
                line-height: 4.14rem;
                text-shadow: 0px 2px 6px rgba(100, 223, 222, 0.6);
                font-family: PingFang-SC-Heavy, PingFang-SC;
                font-weight: 800;
                color: rgba(255, 255, 255, 1);
                letter-spacing: 1px;
            }

            .banner-p2 {
                font-size: 1.09rem;
                line-height: 1.56rem;
                text-shadow: 0px 2px 14px rgba(100, 223, 222, 0.3);
                font-family: PingFang-SC-Heavy, PingFang-SC;
                font-weight: 800;
                color: rgba(100, 223, 222, 1);
            }
        }

        // 联系我们
        .contact-us-txt {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            margin-top: 19%;

            p {

                font-size: 1.88rem;
                line-height: 2.58rem;
                text-shadow: 0px 2px 6px rgba(100, 223, 222, 0.6);
                font-family: PingFang-SC-Heavy, PingFang-SC;
                font-weight: 800;
                color: rgba(255, 255, 255, 1);
            }

            .uploadBtn {
                position: relative;
                width: 82vw;
                height: 22vw;
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: center;
                cursor: pointer;

                .btn-bg {
                    position: absolute;
                    width: 100%;
                    height: 100%;

                }

                .btn-icon {
                    width: 50px;
                    margin-left: 25px;
                    margin-right: 10px;
                }

                span {
                    font-size: 1.88rem;
                    line-height: 2.58rem;
                    text-shadow: 0px 2px 14px rgba(0, 214, 255, 0.5);
                    margin-left: 18px;
                    font-family: PingFangSC-Regular, PingFang SC;
                    font-weight: 400;
                    color: rgba(255, 255, 255, 1);

                }


                .googlePlay {
                    margin-right: 7px;
                }
            }

            .btn1 {
                margin-top: 1rem;
            }

            .btn2 {
                margin-top: .8rem;
            }
        }

    }
}

@media screen and(width: 393px) {

    .homePage-phone {
        main {
            margin-bottom: 40px;

            hgroup {
                padding-top: 10.2rem;

                .banner-p1 {
                    font-size: 2.6rem;
                    line-height: 4.14rem;
                    text-shadow: 0px 2px 6px rgba(100, 223, 222, 0.6);
                }

                .banner-p2 {
                    font-size: .95rem;
                    line-height: 1.56rem;
                    text-shadow: 0px 2px 14px rgba(100, 223, 222, 0.3);
                }

            }
        }

        .contact-us-txt {
            margin-top: 19%;

            p {

                font-size: 1.88rem;
                line-height: 2.58rem;
                text-shadow: 0px 2px 6px rgba(100, 223, 222, 0.6);
            }

            .uploadBtn {
                width: 82vw;
                height: 22vw;

                .btn-icon {
                    width: 50px;
                    margin-left: 25px;
                    margin-right: 10px;
                }

                span {
                    font-size: 1.88rem;
                    line-height: 2.58rem;
                    text-shadow: 0px 2px 14px rgba(0, 214, 255, 0.5);
                    margin-left: 18px;
                }
            }

            .btn1 {
                margin-top: 20px;

            }

            .btn2 {
                margin-top: 10px;
            }

        }
    }

}

